import React, { forwardRef, useState } from 'react';
import { BsEyeSlash, BsEye } from 'react-icons/bs';
import styles from './Input.module.css';

type InputProps = {
  value: string | number;
  name: string;
  handleChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
  type?: string;
  label?: string;
  isEdit?: boolean;
  style?: object;
  inputRef?: any;
  placeHolder?: string | undefined;
  isPassword?: boolean;
  labelStyle?: object;
};

const Input = ({
  handleChange,
  name,
  value,
  type = 'text',
  label = '',
  isEdit = true,
  style = {},
  inputRef,
  placeHolder = '',
  isPassword = false,
  labelStyle = {},
}: InputProps) => {
  const [showInput, setShowInput] = useState(false);
  if (!isEdit) {
    return (
      <>
        {label ? (
          <p className='pb-4 text-sm text-fontColor-light font-thin'>{label}</p>
        ) : null}
        <p
          className={`border-b-2 border-fontColor-darkGray py-1 w-full text-base text-fontColor-light `}
          style={{ height: value ? 'inherit' : '34px' }}
        >
          {value}
        </p>
      </>
    );
  }
  return (
    <>
      {label ? (
        <p className='pb-4 text-sm' style={labelStyle}>
          {label}
        </p>
      ) : null}
      <div className='border border-fontColor-light rounded-md flex items-center'>
        <input
          className={`outline-none rounded-md border-none px-2 py-1 w-full text-base bg-transparent font-thin placeholder-primary-lightest ${styles.input}`}
          value={value}
          name={name}
          onChange={(e) => handleChange(e)}
          type={showInput ? 'text' : type}
          style={{ border: '1px solid black' }}
          ref={inputRef ? inputRef : null}
          placeholder={placeHolder}
        />
        {isPassword ? (
          showInput ? (
            <BsEyeSlash
              className='text-lg text-fontColor-light mx-2 cursor-pointer'
              onClick={() => setShowInput((pre) => !pre)}
            />
          ) : (
            <BsEye
              className='text-lg text-fontColor-light mx-2 cursor-pointer'
              onClick={() => setShowInput((pre) => !pre)}
            />
          )
        ) : null}
      </div>
    </>
  );
};

export default forwardRef(Input);
